.membersContainer {
  padding: 3% 1% 1% 1%;
}
.membersTable {
  width: 80%;
  font-family: "Fira-sans", sans-serif;
  border-collapse: collapse;
  margin: 0 auto;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.tableTitel {
  margin-left: 100px;
}
.membersTable td,
.membersTable th {
  border: 1px solid #ddd;
  text-align: left;
  padding: 8px;
}
.membersTable th {
  background-color: #3dd7ac;
}
