.popup {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  background-color: rgba(0, 0, 0, 0.5);
}

.popup .popup-img {
  position: absolute;
  width: 1000px;
  height: 563px;
  left: 25%;
  right: 25%;
  top: 25%;
  bottom: 25%;
  margin: auto;
  background-color: #cccccc;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.popup .popup-img h2 {
  position: relative;
  top: 25%;
  font-size: 2.5em;
  color: white;
  text-align: center;
  vertical-align: middle;
}
